.header_wrapper {
	height: 40px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.12);
	display: flex;
	align-items: center;
	justify-content: flex-end;
	padding: 0 16px;
	margin-bottom: 16px;
}

.title {
	flex-grow: 1;
	font-weight: 800;
	font-size: 18px;
}

.login_btn {
	color: #fff;
	font-size: 15px;
	outline: none;
	background-color: #42b549;
	border-radius: 8px;
	padding: 4px 16px;
	box-sizing: border-box;
	cursor: pointer;
}

.logout_btn {
	color: #fff;
	font-size: 15px;
	outline: none;
	background-color: red;
	border-radius: 8px;
	padding: 4px 16px;
	box-sizing: border-box;
	cursor: pointer;
	margin-left: 16px;
}
