body {
	margin: 0;
	padding: 0;
	color: rgba(0, 0, 0, 0.7);
	font-size: 14px;
}

table thead td {
	text-align: center;
	font-weight: 600;
	font-size: 16px;
}

.item_wrapper,
.agent_wrapper {
	margin: 40px 16px;
	overflow: auto;
	max-height: 60vh;
}

@media only screen and (max-width: 768px) {
	.item_wrapper,
	.agent_wrapper {
		max-height: 40vh;
	}
}

.item_wrapper table,
.agent_wrapper table {
	border-collapse: collapse;
}

.item_wrapper > table,
.agent_wrapper > table {
	min-width: 900px;
}

.item_wrapper td,
.agent_wrapper td {
	border: 1px solid #e0e0e0;
	padding: 8px;
}

td.edit_active {
	border: 2px solid #42b549;
}

.item_title,
.agent_title {
	margin-bottom: 8px;
	font-size: 16px;
	font-weight: 600;
}

.agent_detail_table td {
	border: none;
}

.agent_detail_table td:first-child {
	width: 150px;
}

.action_column {
	text-align: center;
	width: 120px;
}

.form_table {
	width: calc(100% - 32px);
	margin: 16px;
	border-top: 1px solid #e0e0e0;
	padding-top: 16px;
	margin-top: 16px;
}

.form_table td {
	padding: 3px 0;
}

.form_table td:first-child {
	width: 200px;
}

.form_table button {
	font-size: 12px;
	outline: none;
	border-radius: 8px;
	border: none;
	background-color: #42b549;
	padding: 4px 8px;
	color: #fff;
	cursor: pointer;
	font-weight: 600;
}

.form_table input[type="text"] {
	width: 50%;
	box-sizing: border-box;
}

@media only screen and (max-width: 768px) {
	.form_table input[type="text"] {
		width: 100%;
	}

	input {
		border-radius: 4px;
		border: 1px solid #e0e0e0;
		padding: 4px;
	}
}

.edit_icon {
	display: inline-block;
	margin-right: 20px;
	cursor: pointer;
	width: 28px;
	height: 28px;
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: center;
	background-image: url(data:image/png;base64,);
}

.delete_icon {
	display: inline-block;
	cursor: pointer;
	width: 28px;
	height: 28px;
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: center;
	background-image: url(data:image/png;base64,);
}

.save_icon {
	display: inline-block;
	margin-right: 20px;
	cursor: pointer;
	width: 28px;
	height: 28px;
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: center;
	background-image: url(data:image/png;base64,);
}

.loading {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.6);
	color: #fff;
	font-size: 45px;
	font-weight: 600;
	transition: 1s;
	animation: loading 3s infinite;
}

@keyframes loading {
	50% {
		color: #f2937e;
	}
	,
	100% {
		color: #fff;
	}
}
